<script>


export default {
	name: 'home',
	data(){
		return{
			isLogin:false,
			showGuessPopup:false,
			showPayPasswordPopup:false,
			payPw:'',
			//用户信息
			user_info:{
				money:'238.23'
			},
			//竞猜场次
			guess_data:{
				name:"第0000001期",
				up:0,
				up_money:'',
				down:1,
				down_money:'500',
				end_time:600,
				money_all:'2301',
				result:'up'
			},
			//猜中次数最多
			most_data:{
				name:'小明',
				img:require('../assets/imgs/user.png'),
				money:'2300',
				times:10
			},
			//战绩
			record_data:{
				money:'2361',
				rank:'26',
				true: '12'
			},
			money_item:[20,100,200,500],
			money_item_index:-1,
			money:'',
			guess_type: '1',
			optionline:{
				xAxis: {
					type: 'category',
					show:false
				},
				yAxis: {
					type: 'value',
					show:false,
					min:'dataMin',
					max:'dataMax',
				},
				series: [{
					data: [820, 932, 901, 934,0, 1290, 1330, 1320,880,890,750,260,450,760],
					type: 'line',
					smooth: true,
					animation:false
				}],
				color:["#fff"]
				
			},
			btn_data:{
				btn_now:0,
				btn_pre:0,
				btn_percent:0,
				btn_up:false
			}
		}
	},
	computed:{
		//剩余分钟
		end_minute(){
			let end_minute = parseInt(this.guess_data.end_time/60);
			if(end_minute < 10){
				end_minute = '0' + end_minute;
			}
			return end_minute
		},
		//剩余秒数
		end_second(){
			let end_second = this.guess_data.end_time%60;
			if(end_second < 10){
				end_second = '0' + end_second;
			}
			return end_second
		}
	},
	created(){
		
		if(this.$route.params.id){
			this.utils.info('有id...已登录...');
			this.isLogin = true;
		}else{
			this.utils.info('没有id...未登录...');
			this.isLogin = false;
		}


		this.utils.info('hehe...',this.utils.randomNumber(4));

	},
	mounted(){
		//获取btn信息
		this.getBtnFun();

		//获取当前期数信息
		this.getGuessDataFun();

		//获取用户信息
		this.getUserInfo();
	},
	methods:{
		//返回
		goBack(){
			this.utils.info('返回app...');
			this.$bridge.callhandler('goBack', {tag:"返回app"}, (data) => {})
		},
		//去登录
		goLogin(){
			this.utils.info('去登录...');
			this.$bridge.callhandler('goLogin', {tag:"登录"}, (data) => {})
		},
		//战绩更多
		goMore(){
			this.utils.info('战绩更多');
			this.$bridge.callhandler('goMore', {tag:"战绩更多"}, (data) => {})
		},
		//玩法规则
		goRulePage(){
			this.$router.push({
				name:'rule'
			})
		},
		//显示验证密码
		showPayPassword(){
			this.showPayPasswordPopup = true;
		},
		//关闭验证密码
		closePayPassword() {
			this.showPayPasswordPopup = false
			this.payPw = '';

			this.utils.showToast('您放弃了支付');
		},
		// 数字键盘 事件
		onInput(key) {
			let that = this
			this.payPw = (this.payPw + key).slice(0, 6);
			if (this.payPw.length == 6) {
				let params = {
					payPass: that.payPw
				}
				
			}
		},
		onDelete() {
			this.payPw = this.payPw.slice(0, this.payPw.length - 1);
		},
		//猜涨/跌 type 1涨 2跌
		guessFun(type){
			// if(this.isLogin){
			// 	this.guess_type = type;
			// 	this.showGuessPopup = true;
			// }else{
			// 	this.goLogin();
			// }

			this.guess_type = type;
			this.showGuessPopup = true;
			
		},
		//关闭猜涨/跌
		closeGuessFun(){
			this.showGuessPopup = false;
			this.money = '';
			this.money_item_index = -1;
		},
		//输入竞猜金额
		moneyInputFun(){
			
			this.utils.info('money1...',this.money);
			if(this.money_item_index > -1){
				this.money_item_index = -1;
			}
			if(this.money != ''){
				this.money = parseInt(this.money) + '';

				if(parseInt(this.money) > parseInt(this.user_info.money)){
					this.money = parseInt(this.user_info.money) + '';
				}
			}
			
		},
		//选择竞猜金额
		moneyItemClick(item,index){
			if(this.money_item_index == index){
				return false;
			}
			if(parseInt(item) > parseInt(this.user_info.money)){
				this.utils.showToast('您的蚪金金额不足');
				return false;
			}
			this.money_item_index = index;
			this.money = item;
		},
		//确认猜涨跌
		confirmGuess(){
			if(this.money == ''){
				this.utils.showToast('请输入竞猜金额');
				return false;
			}
			this.closeGuessFun();
			this.showPayPassword();
		},
		//初始化图表
		drawLine(){
	
			let chartline = this.$echarts.init(this.$refs.homeChartContent);
			chartline.setOption(this.optionline);

		},
		//获取btn信息
		getBtnFun(){
			this.$jsonp('https://data.block.cc/api/v1/price/history?symbol_name=bitcoin&limit=50').then(result =>{
				this.utils.info('获取btn信息1...',result);
				let btn_data = result.data;
				let line_data = [];
				for(let i=0;i<btn_data.length;i++){
					line_data.push(btn_data[i][1]);
				}

				this.utils.info('line_data...',line_data);
				this.optionline.series[0].data = line_data;

				this.btn_data.btn_now = line_data[0].toFixed(2);
				this.btn_data.btn_pre = line_data[1].toFixed(2);

				let num = this.btn_data.btn_now - this.btn_data.btn_pre;
				if(num > 0){
					this.btn_data.btn_up = true;
				}else{
					this.btn_data.btn_up = false;
				}
				this.btn_data.btn_percent = ((Math.abs(num))/(this.btn_data.btn_pre) * 100).toFixed(2);

				//初始化图表
				this.drawLine();
			}).catch(err =>{
				this.utils.info('获取btn信息1...err...再次请求',err);

				this.getBtnFun();
			})
			
		},
		//获取当前期数信息
		getGuessDataFun(){
			let end_interval = setInterval(()=>{
				this.guess_data.end_time--;
				if(this.guess_data.end_time == 0){
					clearInterval(end_interval);
				}
			},1000)
		},
		//获取用户信息
		getUserInfo(){
			this.http.post(this.http._HOST + 'login/user_detail',{user_id:'dy11234'}).then(result =>{
				this.utils.info('获取用户信息...',result);
			})
		}
	},
	beforeRouteEnter(to,from,next){
		console.log('to...',to);
		console.log('from...',from);
		next();
	}
}
</script>


